body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'Questrial', sans-serif;
}
.background{
    width: 100vw;
    height: 100vh;
    background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background-size: 500% 500%;
    animation: blend 20s ease-in infinite alternate-reverse;
}
@-webkit-keyframes blend {
    0% {
        background-position: 0% 0%;
    }
    33%{
        background-position: 100% 0%;
    }
    66%{
        background-position: 0% 100%;
    }
    100% {
        background-position:100% 100%;
    }
}
@keyframes blend {
    0% {
        background-position: 0% 0%;
    }
    33%{
        background-position: 100% 0%;
    }
    66%{
        background-position: 0% 100%;
    }
    100% {
        background-position:100% 100%;
    }
}
.logo{
    height: 100%;
    width: 100%;
    background: url(/static/media/logo.649d08ee.svg) center;
    background-size: contain;
}
h4{
    color: #7050CC !important;
    font-weight: bold;
    text-align: center;
}
a{
    text-decoration: none !important;
}
a .input{
    height: 50px;
    width: 100%;
    font-family: 'Lato';
    font-size: 1em;
    font-weight: 500;
    color: #7050CC;
    background-color: white;
    border-radius: 2px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);
    -webkit-transition: all 200ms;
    transition: all 200ms;
}
.input > span > span{
    color: #B2E3FF;
}
.component-holder{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    grid-row-gap: 50px;
    row-gap: 50px;
    padding: 80px 60px 30px 60px;
}
.input:hover{
    background-color: #eeeeff;
}
.icon{
    width: 50px;
    border: 1px solid #aaaaaa !important;
    margin-left: calc(50vw - 25px)!important;
}
@media screen and (min-width: 600px){
    .component-holder{
        padding: 80px 80px 30px 80px;
    }
    .icon{
        width: 50px;
        margin-left: 175px !important;
    }
}
h5{
    color: #7050CC !important;
    box-sizing: border-box;
    padding-top: 30px;
    text-align: center;
}
.create-holder{
    display: grid;
    grid-template-rows: 60px calc( 100vh - 140px );
}
.component-holder--screen{
    height: 100%;
    display: grid;
    grid-template-rows: repeat(auto, 1fr);
    padding: 0 60px 0 60px;
}
@media screen and (min-width: 600px){
    h5{
        padding-top: 50px;
    }
    .create-holder{
        display: grid;
        grid-template-rows: 100px calc( 80vh - 180px );
    }
    .component-holder--screen{
        height: 100%;
        display: grid;
        grid-template-rows: repeat(auto, 1fr);
        padding: 0 80px 0 80px;
    }
}
.logo{
    height: 20vh;
    width: 20vh;
    margin: 8vh auto 5vh auto;
}
.app-interface{
    position: fixed; 
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(255, 255, 255, 0.5) !important;
}
@media screen and (min-width: 600px){
    .app-interface{
        top: 5vh;
        left: calc( 50vw - 200px );
        height: 90vh;
        width: 400px;
        background-color: rgba(255, 255, 255, 0.8) !important;
    }
}
a img {
    height: auto;
    width: 20%;
    margin: 10px calc( 20%/8 - 2px ) 40px calc( 20%/8 - 2px );
    border-radius: 4px;
    border: 2px solid grey;
}
a img:hover{
    border-color: rgb(0, 177, 0);
}
.share{
    position: fixed;
    top: 20vh;
    left: 10vw;
    width: 80vw;
    background-color: white;
}
.share--content{
    padding: 40px 15% 0 15%;
}
.share-plan--header{
    width: 100%;
    height: 80px;
    background-color: #eaf0f3;
    font-family: 'Archivo Black', sans-serif;
    font-weight: bold;
    font-size: 1.25em;
    text-align: center;
    padding-top:calc( 40px - 0.5em);
    box-sizing: border-box;
}
.copy-link{
    height: 1.5em;
    white-space: nowrap;
    overflow-y: scroll;
    font-family: 'Lato';
    background-color: #eaf0f3;
    padding: 0.5em;
    margin: 10px 0 20px 0;
    border: 2px solid rgb(169, 191, 214);
    border-radius: 5px;
}
@media screen and (min-width: 600px){
    .share{
        position: fixed;
        top: 20vh;
        left: calc(50vw - 275px);
        width: 550px;
    }
    .share-plan--header{
        font-size: 1.5em;
    }
}
.selection-list--title{
    margin-left: 25px;
}
.selection-list{
    width: calc( 100vw - 50px);
    margin-left: 25px;
    box-sizing: border-box;
    padding: 15px;
    white-space: nowrap;
    overflow-x: scroll;
    height: 65px;
}
.chip{
    margin: 2px;
}
@media screen and (min-width: 600px){
    .selection-list{
        width: 350px;
    }
}
:root{
    --calendar-day-height: calc( var(--calendar-height) / 7 );
    --calendar-day-width: calc( var(--calendar-width) / 7 );
}
#dates--interactive{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    height: 100%;
    width: 100%;
}
.day-disabled{
    font-weight: 300 !important;
}
.day-name{
    color: black !important;
}
.day-today{
    color: #81afd8 !important;
}
.day-today.day-selected{
    color: white !important;
}
.day-left{
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%;
}
.day-left:before{
    border-color: transparent !important;
}
.day-selected:before{
    display: block;
    content: '';
    position: absolute;
    top: 0;
    left: -4px;
    height: calc( var(--calendar-width) / 7 );
    height: var(--calendar-day-width);
    width: 0;
    border-style: solid;
    border-width: 0 4px 0 0;
    border-color: rgb(142, 226, 73);
}
.day-selected:after{
    right: -4px;
    border-color: transparent rgb(142, 226, 73);
}
.day-right{
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
}
.day-right:after{
    border-color: transparent !important;
}
.day-wrap{
    float: left;
}
.calendar__day{
    float: left;
    text-align: center;
    height: calc( var(--calendar-width) / 7 );
    height: var(--calendar-day-width);
    width: calc( var(--calendar-width) / 7 );
    width: var(--calendar-day-width);
    margin-bottom: calc( calc( var(--calendar-height) / 7 ) - calc( var(--calendar-width) / 7 ));
    margin-bottom: calc( var(--calendar-day-height) - var(--calendar-day-width) );
    padding-top: calc( calc( var(--calendar-width) / 7 ) / 2 - 7px );
    padding-top: calc( var(--calendar-day-width) / 2 - 7px );
    box-sizing: border-box;
    font-weight: 900;
    color: black;
    -webkit-transition: box-shadow 0.5s, border-radius 0.5s, background-color 0.5s, color 0.5s;
    transition: box-shadow 0.5s, border-radius 0.5s, background-color 0.5s, color 0.5s;
}
.calendar__day:before:not(.day-selected){
    left: 0px;
    border-color: transparent;
}
.calendar__day:not(.day-selected){
    border-radius: 50% !important;
}
.calendar__day:before{
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
.calendar__day:after{
    display: block;
    content: '';
    position: absolute;
    top: 0;
    right: 0px;
    height: calc( var(--calendar-width) / 7 );
    height: var(--calendar-day-width);
    width: 0;
    border-style: solid;
    border-width: 0 4px 0 0;
    border-color: transparent;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
.calendar__day--clear{
    clear: both;
}
:root{
    --calendar-width: calc( 100vw - 50px);
    --calendar-height: 58vh;
}
.calendar-title{
    font-family: 'Lato';
    height: 8vh;
    text-align: center;
    display: grid;
    grid-template-columns: 25% 50% 25%;
}
#calendar-title--text{
    font-size: 1.3em;
    box-sizing: border-box;
    padding-top: calc( 4vh - 0.6em);
}
.calendar-title span{
    color: #5900a3;
    font-size: 0.75em !important;
}
.calendar-arrows{
    box-sizing: border-box;
    padding-top: calc( 2.5vh - 0.6em);
}
#calendar{
    height: 58vh;
    height: var(--calendar-height);
    width: calc( 100vw - 50px);
    width: var(--calendar-width);
    margin: auto;
}
.day-selected{
    position: relative;
    box-shadow: -0.5px 1.5px 3px #333333;
    color: white;
    background-color: rgb(142, 226, 73);
}
.day-selected.partial{
    background-color: #ffa726;
}
.day-selected.full{
    background-color: #1e88e5;
}
.day-disabled{
    pointer-events: none;
    color: grey;
}
.buttons-suite{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
    height: calc( 92vh - (58vh + 125px));
    height: calc( 92vh - (var(--calendar-height) + 125px));
    width: calc( 100vw - 50px);
    width: var(--calendar-width);
    border-radius: 2px;
    background-color: grey;
    mix-blend-mode: darken;
    margin: 20px 25px 20px 25px;
}
a > .button-suite--button{
    height: 100%;
    width: 100%;
    border: 0px solid rgba(0, 0, 0, 0.1);
    -webkit-transition: all 300ms;
    transition: all 300ms;
    border-radius: 0;
}
a > .button-suite--button:hover{
    height: 100%;
    width: 100%;
    border: 7px solid rgba(0, 0, 0, 0.1);
}
a > .button-suite--button:active{
    height: 100%;
    width: 100%;
    border: 7px solid rgba(0, 0, 0, 0.5);
}
.selected-color{
    background-color: rgb(144, 224, 79) !important;
}
.today-color{
    background-color: #4a148c !important;
}
.full-color{
    background-color: #81afd8 !important;
}
.partial-color{
    background-color: #f7b758 !important;
}
@media screen and (min-width: 350px){
    #calendar-title--text{
        font-size: 1.5em;
    }
}
@media screen and (min-width: 600px){
    .buttons-suite{
        height: calc( 82vh - (58vh + 125px));
        height: calc( 82vh - (var(--calendar-height) + 125px));
    }
    #calendar-title--text{
        font-size: 1.8em;
    }
    :root{
        --calendar-width: 350px;
        --calendar-height: 55vh;
    }
}
#view-socials{
    position: fixed; 
    box-sizing: border-box;
    padding: 20px;
    top: 0;
    left: 0;
    height: calc( 100vh - 50px);
    width: 100vw;
    overflow-y: scroll;
}
.view-back{
    position: fixed;
    text-align: center;
    bottom: 0;
    width: 100vw;
}
@media screen and (min-width: 600px){
    #view-socials{
        top: 5vh;
        left: calc( 50vw - 200px );
        height: calc( 90vh - 50px );
        width: 400px;
    }
    .view-back{
        bottom: calc( 5vh );
        width: 400px;
    }
}

.app-interface{
    position: fixed; 
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(255, 255, 255, 0.6) !important;
}
@media screen and (min-width: 600px){
    .app-interface{
        top: 5vh;
        left: calc( 50vw - 200px );
        height: 90vh;
        width: 400px;
        background-color: rgba(255, 255, 255, 0.8) !important;
    }
}

